import React, { useState, useEffect } from "react";
import { useForm } from "react-hook-form";
import { BeatLoader } from "react-spinners";
import validator from "validator";
const sleep = (ms) => new Promise((resolve) => setTimeout(resolve, ms));
const MyForm = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
    formState,
  } = useForm();
  const [sent, setSent] = useState(false);
  const [success, setSuccess] = useState(false)
  const [spinnerState, setSpinnerState] = useState(false);
  const [nameWarn, setNameWarn] = useState("");
  const [emailWarn, setEmailWarn] = useState("");
  const [infoWarn, setInfoWarn] = useState("");
  const [emailState, setEmailState] = useState(false);
  const [nameState, setNameState] = useState(false);
  const [infoState, setInfoState] = useState(false);
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [info, setInfo] = useState("");
  const [warnState, setWarnState] = useState(false);
  const [agree, setAgree] = useState(false);
  const onSubmit = (data) => {
    validateName();
    validateEmail();
    validateInfo();
    setWarnState(true);
  };

  const handleClose = () => {
    document.getElementsByClassName("form")[0].style.display = "none";
    document.body.style.overflow = "auto";
    document.body.style.touchAction = "auto";
    document.body.style.position = "relative";

    setSent(false);
    setNameState(false);
    setEmailState(false);
    setInfoState(false);
    setWarnState(false);
    setAgree(false);
  };
  const validateEmail = () => {
    if (validator.isEmail(email)) {
      setEmailState(true);
    } else {
      setEmailWarn("Please enter a valid email address");
      setEmailState(false);
    }
  };
  const validateName = async () => {
    if (/^[A-Za-z ]+$/.test(name)) setNameState(true);
    else {
      setNameWarn("Name can only contain letters and spaces");
      setNameState(false);
    }
  };
  const validateInfo = async () =>{
    if(info.length <= 1000) setInfoState(true);
    else{
      setInfoWarn("Info must be less than 1000 characters");
      setInfoState(false);
    }
  }
  const handleName = (e) => {
    setName(e.target.value);
  };
  const handleEmail = (e) => {
    setEmail(e.target.value);
  };
  const handleInfo = (e) =>{
    setInfo(e.target.value);
  }
  const handleAgree = (e) => {
    setAgree(!agree);
  };
  useEffect(() => {
    if (emailState && nameState && infoState) {
      (async () => {
        try {
          setSpinnerState(true);
          const date = `${new Date().getFullYear()}-${new Date().getMonth()+1}-${new Date().getDate()}`;
            const userAgent = navigator.userAgent;
            const language = navigator.language;
            const ipData = await fetch('https://api.ipify.org?format=json').then(response => response.json())
            const data = {
              fullName: name,
              email: email,
              info: info,
              subscriptionDate: date,
              userAgent: userAgent,
              ip:ipData.ip,
              systemLanguage: language,
            }
            const req = await fetch('https://api.majordom.io/users',{
              method: "POST",
              headers: {'Content-Type': 'application/json'}, 
              body: JSON.stringify(data)
            });
            setSpinnerState(false);
            setSuccess(200 <= req.status < 300)
            setSent(true);
        } catch {
          setSpinnerState(false);
          setSuccess(false)
          setSent(true);
        }
      })();
    }
  }, [nameState, emailState, infoState]);
  return (
    <div className="form">
      <form onSubmit={handleSubmit(onSubmit)}>
        <div className="title">
          {sent ? (success?"Welcome aboard!":"Oops") : "Join the Inner Circle!"}
        </div>
        <div className="subtitle">
          {sent
            ? (success?"Were thrilled to have you. Keep an eye on your inbox for exciting updates.":"Something went wrong, please try again later")
            : "Subscribe now and be the first to receive exclusive updates, news, and early-bird offers on our upcoming product MajorDom!"}
        </div>
        {!sent && (
          <div className="input__container">
            <input
              type="text"
              placeholder="Name"
              required
              onChange={handleName}
            />
            {!nameState && warnState && <span>{nameWarn}</span>}
          </div>
        )}
        {!sent && (
          <div className="input__container">
            <input
              type="email"
              placeholder="Email"
              required
              className="form-email"
              onChange={handleEmail}
            />
            {warnState && !emailState && infoState && nameState && <span>{emailWarn}</span>}
          </div>
        )}
        {!sent && (
          <div className="input__container">
          <input
            onChange={handleInfo}
            placeholder="Additional Info. Here you can specify your experience, requirements, request features, or ask questions."
            maxLength={1000}
            />    
            {warnState && !infoState && emailState && nameState && <span>{infoWarn}</span>}
            </div>
        )}
        {!sent && (
          <div className="checkbox__container">
            <input
              type="checkbox"
              checked={agree}
              onChange={handleAgree}
              required
            />
            I agree to receive updates, news, and promotional emails about
            MajorDom. I understand I can unsubscribe at any time.
          </div>
        )}
        {sent ? (
          <a className="submit-btn" onClick={() => handleClose()}>
            Done
          </a>
        ) : (
          <button type="submit" className="submit-btn">
            Count Me In!
          </button>
        )}
        {spinnerState && <BeatLoader color="#fff" />}
      </form>

      <button className="close-btn" onClick={() => handleClose()}>
        <img src="/images/close.svg" alt="not found" width={40} height={40} />
      </button>
    </div>
  );
};

export default MyForm;
